.main {

  &__content {
    display: grid;
    row-gap: $section-gap-mobile;
    padding-block: $section-gap-mobile;

  }

  @include on-tablet {
    row-gap: $section-gap-tablet;
    padding-block: $section-gap-tablet;
  }

  @include on-desktop {
    row-gap: $section-gap-desktop;
    padding-block:$section-gap-desktop;
  }
}
