@mixin on-tablet {
  @media (min-width: $tablet-min-width) {
    @content;
  }
}

@mixin on-desktop {
  @media (min-width: $desktop-min-width) {
    @content;
  }
}

@mixin hover($property, $toValue) {
  transition: #{$property} $effectDuration;
  &:hover {
    #{$property}: #{$toValue};
  }
}

@mixin font-header-1 {
  font-size: $font-size-header-h1-mobile;
  line-height: $font-size-header-h1-mobile;

  @include on-tablet {
    font-size: $font-size-header-h1-tablet;
    line-height: $font-size-header-h1-tablet;
  }

  @include on-desktop {
    font-size: $font-size-header-h1-desktop;
    line-height: $font-size-header-h1-desktop;
  }
}

@mixin content-padding-inline {
  padding-inline: $padding-inline-content-mobile;

  @include on-tablet {
    margin-inline: auto;
    padding-inline: $padding-inline-content-tablet;
  }

  @include on-desktop {
    padding-inline: $padding-inline-content-desktop;
  }
}

@mixin page-grid {
  --columns: 2;

  display: grid;
  grid-template-columns: repeat(var(--columns), 1fr);

  @include on-tablet {
    --columns: 6;

    column-gap: 24px;
  }

  @include on-desktop {
    --columns: 12;
  }
}

@mixin hover-scale-up {
  &:hover {
    cursor: pointer;
    transform: scale(1.05)
  }
}
